import "../../styles/Chat/NewChat.css";
import { useState } from "react";
import Modal from "react-modal";
import Cookies from "js-cookie";

const customStyles = {
  overlay: {
    backgroundColor: "rgba(0,0,0,0.5)",
  },
};

const NewChat = ({ isOpen, closeModal, fetchChatRooms }) => {
  const [selectedOption, setSelectedOption] = useState("");
  const [chatName, setChatName] = useState("");
  const [password, setPassword] = useState("");
  const [hashtag, setHashtag] = useState("#");
  const [nameError, setNameError] = useState(false);
  const [passwordError, setPasswordError] = useState(false);
  const [hashtagError, setHashtagError] = useState(false);

  //
  const validateField = (fieldName) => {
    const value = document.getElementById(fieldName).value;
    switch (fieldName) {
      case "name":
        setNameError(value.trim() === "");
        break;
      case "password":
        setPasswordError(value.trim() === "");
        break;
      case "hashtag":
        setHashtagError(value.trim() === "#" || value.trim() === ""); 
        break;
      default:
        break;
    }
  };

  const handleChange = (e) => {
    setSelectedOption(e.target.value);
  };

  // # 
  const handleHashtagChange = (e) => {
    const value = e.target.value;
    if (value === "" || value === "#") {
      setHashtag("#"); // #  
    } else if (!value.startsWith("#")) {
      setHashtag("#" + value); //   # 
    } else {
      setHashtag(value);
    }
  };

  //   
  const handleSubmit = async (e) => {
    e.preventDefault();

    if (chatName.trim() === "") {
      setNameError(true);
      return;
    }

    if (selectedOption === "" && password.trim() === "") {
      setPasswordError(true);
      return;
    }

    if (hashtag.trim() === "#" || hashtag.trim() === "") {
      setHashtagError(true);
      return;
    }

    const chatRoomData = {
      roomName: chatName,
      chatType: "GROUP_CHAT",
      password: selectedOption === "" ? password : null,
      hashtag: hashtag,
      isSecret: selectedOption === "",
    };

    const token = Cookies.get("accessToken");

    try {
      const response = await fetch("/api/chat/rooms", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(chatRoomData),
      });

      if (response.ok) {
        console.log("  .");
        closeModal();
        fetchChatRooms();
      } else {
        console.error("  .");
      }
    } catch (error) {
      console.error(" .:", error);
    }
  };

  return (
    <Modal
      isOpen={isOpen}
      onRequestClose={closeModal}
      style={customStyles}
      shouldCloseOnOverlayClick={false}
      className="new-chat__modal"
    >
      <div className="new-chat__wrapper">
        <form className="new-chat__form" onSubmit={handleSubmit}>
          <div className="new-chat__holder">
            <input
              className="new-chat__input"
              type="text"
              name="name"
              id="name"
              placeholder="  ."
              value={chatName}
              onChange={(e) => setChatName(e.target.value)}
            />
          </div>
          {nameError && (
            <p className="error-message">  .</p>
          )}
          <input
            className="new-chat__input"
            type="text"
            name="hashtag"
            id="hashtag"
            value={hashtag}
            onChange={handleHashtagChange}
          />
          <p className="new-chat__hashtag">
            #  .
          </p>
          {hashtagError && (
            <p className="error-message"> .</p>
          )}
          <p className="new-chat__subtitle"> ?</p>
          <div className="new-chat__group">
            <div>
              <input
                type="radio"
                name="privateRoom"
                id="yes"
                value=""
                checked={selectedOption === ""}
                onChange={handleChange}
              />
              <label
                className="new-chat__subtitle new-chat__subtitle--label"
                htmlFor="yes"
              >
                
              </label>
            </div>
            <div>
              <input
                type="radio"
                name="privateRoom"
                id="no"
                value=""
                checked={selectedOption === ""}
                onChange={handleChange}
              />
              <label
                className="new-chat__subtitle new-chat__subtitle--label"
                htmlFor="no"
              >
                
              </label>
            </div>
          </div>
          {selectedOption === "" && (
            <div>
              <input
                className="new-chat__input new-chat__input--pw"
                type="password"
                name="password"
                id="password"
                placeholder=" ."
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
              {passwordError && (
                <p className="error-message"> .</p>
              )}
            </div>
          )}
          <div className="new-chat__wrapper">
            <button className="new-chat__button" type="submit">
               
            </button>
            <button className="new-chat__button" onClick={closeModal}>
              
            </button>
          </div>
        </form>
      </div>
    </Modal>
  );
};

export default NewChat;
