import React, { useContext } from 'react'
import '../styles/Header.css'
import { NavLink } from "react-router-dom";
import { Link } from 'react-router-dom'
import { LoginContext } from '../contexts/LoginContextProvider'

const Header = () => {

    //  isLogin   :   - Y(true), N(false)
    //  logout()  :   - setLogin(false)
    const { isLogin, login, logout } = useContext(LoginContext);
    
    return (

 <header className="header">
      <div className="logo">
        NET<span>WORKING</span>
      </div>
      <nav>
        <NavLink exact to="/" activeClassName="active">
          
        </NavLink>
        <NavLink to="/joblist" activeClassName="active">
           
        </NavLink>
        <NavLink to="/chat" activeClassName="active">
          
        </NavLink>
        <NavLink to="/notifications" activeClassName="active">
          
        </NavLink>
</nav>
            <div className="util">
                {
                    !isLogin 
                    ? 
                    /*   */
                    <ul>
                        <li><NavLink to="/login"></NavLink>&nbsp;&nbsp;&nbsp;
			<NavLink to="/join"></NavLink></li>
                    </ul>
                    :
                    /*   */
		    <ul>
		      <li>
                         <NavLink to="/User"></NavLink>&nbsp;&nbsp;&nbsp;
                         <button className="Link" onClick={ () => logout() }></button></li>
                      </ul>
                }
            </div>
        </header>
    )
}

export default Header