import React, { createContext, useEffect, useState } from 'react';
import api from '../apis/api';
import Cookies from 'js-cookie';
import * as auth from '../apis/auth';
import { useNavigate } from 'react-router-dom';
import * as Swal from '../apis/alert';

export const LoginContext = createContext();
LoginContext.displayName = 'LoginContextName'

/**
 *   
 *    
 *   
 *   
 *  
 *    
 *    
*/
const LoginContextProvider = ({ children }) => {
    /*
        
        -  
        -  
        -  
        -  
    */
   /* -----------------------[State]-------------------------- */
    //  
    const [isLogin, setLogin] = useState(false);

    //  
    const [userInfo, setUserInfo] = useState(null)

    //  
    const [roles, setRoles] = useState({isUser : false, isAmdin : false})

    //  
    const [remberUserId, setRemberUserId] = useState()
    /* -------------------------------------------------------- */

    //  
    const navigate = useNavigate()


    /* 
          
        -  jwt   
        - jwt    
    */
    const loginCheck = async () => {

        //     jwt  
        const accessToken = Cookies.get("accessToken")
        console.log(`accessToekn : ${accessToken}`);

        
        // accessToken (jwt)  
        if( !accessToken ) {
            console.log(` accessToken(jwt)  `);
            //  
            logoutSetting()
            return
        }
        
        // accessToken (jwt)  
        //   header   jwt 
        api.defaults.headers.common.Authorization = `Bearer ${accessToken}`

        //    
        let response
        let data

        try {
            response = await auth.info()
        } catch (error) {
            console.log(`error : ${error}`);
            console.log(`status : ${response.status}`);
            return;
        }

        data = response.data
        console.log(`data : ${data}`);

        //   
        if( data == 'UNAUTHRIZED' || response.status == 401 ) {
            console.error(`accessToken (jwt)    .`);
            return
        }

        //   
        console.log(`accessToek (jwt)     !`);

        //  
        loginSetting(data, accessToken)
    }

    //   
    const login = async (username, password) => {

        console.log(`username : ${username}`);
        console.log(`password : ${password}`);

        try {
            const response = await auth.login(username, password)
            const data = response.data
            const status = response.status
            const headers = response.headers
            const authroization = headers.authorization
            const accessToken = authroization.replace("Bearer ", "")  //  JWT

            console.log(`data : ${data}`);
            console.log(`status : ${status}`);
            console.log(`headers : ${headers}`);
            console.log(`jwt : ${accessToken}`);

            //   
            if( status === 200 ) {
                //     accessToken(jwt) 
                Cookies.set("accessToken", accessToken)

                //   ( /users/{userId}   <---  userData )
                loginCheck()
                
                // 
                // alert(` `)
                Swal.alert(` `, `  .`, "success", () => { navigate("/")})

                //   
                navigate("/")
            }
        } catch (error) {
            //  
            // -     .
            Swal.alert(" ", "    .", "error" )
        }
        

    }

    //  
    const logout = (force=false) => {

        if( force ) {
            //  
            logoutSetting()
        
            //    "/" ()
            navigate("/")
            return
        }        

        // const check = window.confirm(`?`)

        Swal.confirm("?", " .", "warning",
                (result) => {
                    if( result.isConfirmed ) {
                        //  
                        logoutSetting()

                        //   
                        navigate("/")
                    }
                }
            )

        // if( check ) {
        //     //  
        //     logoutSetting()

        //     //   
        //     navigate("/")
        // }

    }

    //   
    //  userData,   accessToken (jwt)
    const loginSetting = (userData, accessToken) => {

        const { no, userId, authList } = userData
        const roleList = authList.map((auth) => auth.auth)

        console.log(`no : ${no}`);
        console.log(`userId : ${userId}`);
        console.log(`authList : ${authList}`);
        console.log(`roleList : ${roleList}`);

        //  axios  header(Authorization : `Bearer ${accessToken}`)
        api.defaults.headers.common.Authorization = `Bearer ${accessToken}`;

        //    : true
        setLogin(true)
        
        //   
        const updatedUserInfo = {no, userId, roleList}
        setUserInfo(updatedUserInfo)

        //   
        const updatedRoles = { isUser : false, isAmdin : false }

        roleList.forEach( (role) => {
            if( role == 'ROLE_USER' ) updatedRoles.isUser = true
            if( role == 'ROLE_ADMIN' ) updatedRoles.isAdmin = true
        })
        setRoles(updatedRoles)
    }

    //  
    const logoutSetting = () => {
        //  axios  
        api.defaults.headers.common.Authorization = undefined;

        //   
        Cookies.remove("accessToken")

        //    : false
        setLogin(false)

        //    
        setUserInfo(null)

        //    
        setRoles(null)
    }

    

    useEffect( () => {
      
        //  
        loginCheck()
      
    }, [])



    return ( 
        <LoginContext.Provider value={ {isLogin, userInfo, roles, login, loginCheck, logout} }>
            {children}
        </LoginContext.Provider>
    )
}

export default LoginContextProvider