import React from 'react'

const JoinForm = ({ join }) => {

    const onJoin = (e) => {
        e.preventDefault()      // submit   
        const form = e.target
        const userId = form.username.value      // 
        const userPw = form.password.value      // 
        const name = form.name.value            // 
        const email = form.email.value          // 
        const area = form.area.value            // 
        const status = form.status.value        // 
        const industry = form.industry.value    // 
        const edu = form.edu.value              // 
        const skill = form.skill.value          // 
        const cert = form.cert.value            // 
        const bio = form.bio.value              // 
        const company = form.company.value      // 
        const title = form.title.value          // 

        console.log(userId, userPw, name, email, area, status, industry, edu, skill, cert, bio, company, title);

        join( {userId, userPw, name, email, area, status, industry, edu, skill, cert, bio, company, title} )
    }

  return (
    <div className="form">
        <h2 className="login-title"></h2>

        <form className='login-form' onSubmit={ (e) => onJoin(e) }>
            <div>
                <label htmlFor="username"></label>
                <input type="text"
                        id='username'
                        name='username'
                        required
                />
            </div>

            <div>
                <label htmlFor="password"></label>
                <input type="password"
                        id='password'
                        name='password'
                        required
                />
            </div>

            <div>
                <label htmlFor="name"></label>
                <input type="text"
                        id='name'
                        name='name'
                        required
                />
            </div>

            <div>
                <label htmlFor="email"></label>
                <input type="email"
                        id='email'
                        name='email'
                        required
                />
            </div>

            <div>
                <label htmlFor="area"></label>
                <input type="text"
                        id='area'
                        name='area'
                />
            </div>

            <div>
                <label htmlFor="status"></label>
                <input type="text"
                        id='status'
                        name='status'
                />
            </div>

            <div>
                <label htmlFor="industry"></label>
                <input type="text"
                        id='industry'
                        name='industry'
                />
            </div>

            <div>
                <label htmlFor="edu"></label>
                <input type="text"
                        id='edu'
                        name='edu'
                />
            </div>

            <div>
                <label htmlFor="skill"></label>
                <input type="text"
                        id='skill'
                        name='skill'
                />
            </div>

            <div>
                <label htmlFor="cert"></label>
                <input type="text"
                        id='cert'
                        name='cert'
                />
            </div>

            <div>
                <label htmlFor="bio"></label>
                <input type="text"
                        id='bio'
                        name='bio'
                />
            </div>

            <div>
                <label htmlFor="company"></label>
                <input type="text"
                        id='company'
                        name='company'
                />
            </div>

            <div>
                <label htmlFor="title"></label>
                <input type="text"
                        id='title'
                        name='title'
                />
            </div>

            <button type='submit' className='btn btn--form btn-login'>
                                
            </button>
        </form>
    </div>
  )
}

export default JoinForm